import {makeAutoObservable} from "mobx"

export default class UserStore{

    constructor(){
        this._isAuth=false;
        this._user={};
        makeAutoObservable(this)//        UserStore
    }

    setIsAuth(bool){
        this._isAuth=bool;
    }//Action    isAuth

    setUser(user){
        this._user=user;
    }//Action    

    get isAuth(){
        return this._isAuth
    }

    get user(){
        return this._user
    }
}

